/* App.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f4f4f4;
}

.app-container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.tree-container {
  width: 200px;
  padding: 10px;
  border-right: 2px solid #eaeaea;
}

.gender {
  margin: 10px 0;
}

.gender strong {
  font-size: 1.2em;
  color: #333;
}

.tree ul {
  list-style-type: none;
  padding-left: 15px;
}

.tree li {
  cursor: pointer;
  padding: 5px;
  transition: background-color 0.2s;
}

.tree li:hover {
  background-color: #f0f0f0;
}

.table-container {
  margin-left: 20px;
  flex: 1;
}

.client-table {
  width: 100%;
  border-collapse: collapse;
}

.client-table th,
.client-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.client-table th {
  background-color: #4caf50;
  color: white;
}

.client-table tr:hover {
  background-color: #f1f1f1;
}
